
<template>
  <div class="bg-gray-100">
    <div id="wrapper" class="flex flex-col justify-between h-screen">
      <!-- header-->
      <subHeader ref="subHeader"></subHeader>

      <!-- Content-->
      <div>
        <div
          class="lg:p-12 max-w-md max-w-xl lg:my-0 my-12 mx-auto p-6 space-y-"
        >
          <h1 class="lg:text-3xl text-xl font-semibold mb-6">登 录</h1>
          <p class="mb-2 text-black text-lg">请输入用户名和密码</p>
          <form action="">
            <input
              type="text"
              placeholder="example@mydomain.com"
              v-model="username"
              class="bg-gray-200 mb-2 shadow-none dark:bg-gray-800"
              style="border: 1px solid #d3d5d8 !important"
            />
            <input
              type="text"
              placeholder="***********"
              v-model="password"
              class="bg-gray-200 mb-2 shadow-none dark:bg-gray-800"
              style="border: 1px solid #d3d5d8 !important"
            />
            <div class="flex justify-between my-4">
              <div class="checkbox">
                <input type="checkbox" id="chekcbox1" checked />
                <label for="chekcbox1"
                  ><span class="checkbox-icon"></span>记住密码</label
                >
              </div>
              <a href="#" @click="forgetThePassword()">忘记密码? </a>
            </div>
            <div
              class="bg-gradient-to-br from-pink-500 py-3 rounded-md text-white text-xl to-red-400 w-full loginBtn"
              @click="login()"
            >
              登录
            </div>
            <div class="text-center mt-5 space-x-2">
              <p class="text-base">
                没有注册用户?
                <a href="#" @click="toRegister()" style="color: #3a79d8">
                  创建一个账号
                </a>
              </p>
            </div>
          </form>
        </div>
      </div>
      <!-- Footer -->
      <subFooter></subFooter>
    </div>
  </div>
</template>

<script>
import subHeader from "@/components/subHeader";
import subFooter from "@/components/subFooter";
export default {
  name: "login",
  components: { subHeader, subFooter },
  data() {
    return {
      username: "18792800701@163.com",
      password: "3930153652",
      value: "",
    };
  },
  methods: {
    login() {
      this.$router.push({ name: "profile" });
    },
    toRegister() {
      this.$router.push({ name: "register" });
    },
    forgetThePassword() {
      this.$toast('请联系管理员！');
    },
  },
  mounted() {
    this.$refs.subHeader.isActiveLogin = true;
    this.$refs.subHeader.isActiveRegister = false;
  },
};
</script>

<style lang="scss" scoped>
.loginBtn {
  text-align: center;
}
</style>



