
<template>
  <!-- header-->
  <div class="py-4 shadow" :class="getdarkTheme==true?'bg-gray-800':'bg-white'">
    <div class="max-w-6xl mx-auto">
      <div class="flex items-center lg:justify-between justify-around">
        <a href="javascript:void(0)" @click="toTrending()">
          <img src="../assets/images/logo.png" alt="" class="w-32" />
        </a>
        <div
          class="capitalize flex font-semibold hidden lg:block my-2 space-x-3 text-center text-sm"
        >
          <a href="javascript:void(0)" @click="toLogin()" :class="isActiveLogin?activeBtn:noActiveBnt"
            >登录</a
          >
          <a
            href="javascript:void(0)"
            :class="isActiveRegister?activeBtn:noActiveBnt"
            @click="toRegister()"
            >注册</a
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "subHeader",
  data() {
    return {
      activeBtn:"bg-pink-500 pink-500 px-6 py-3 rounded-md shadow text-white",
      noActiveBnt:"py-3 px-4",
      isActiveLogin:false,
      isActiveRegister:false,
    };
  },
  computed: {
    ...mapGetters(["getdarkTheme"]),
  },
  mounted() {
  },
  methods: {
    toLogin() {
      this.$router.push({ name: "login" });
    },
    toRegister() {
      this.$router.push({ name: "register" });
    },
    toTrending() {
      this.$router.push({ name: "trending" });
    },
  },
};
</script>

<style lang="scss" scoped>

</style>



